import React, { useEffect, useState } from "react";
import axios from "axios";
import { backend_base_url } from "../config/urls";
import { useParams } from "react-router-dom";

function Success(props) {
  console.log(props)
  const [data, setData] = useState({})
    const params = useParams();
    console.log(params);
  useEffect(() => {
    axios({
      method: 'get',
      headers: {
        'Content-type': "application/json"
      },
      url: backend_base_url+`/txn/${params.txn}`,
    }).then(res => {
      setData(res.data);
    }).catch(err => {
      console.log(err)
    })
  },[params.txn])
  return (
    <div>
      <p>
        Product : <span>{data.productinfo}</span>
      </p>
      <p>
        Amount : <span>{data.amount} INR</span>
      </p>
      <h1> transaction status : {data.status}</h1>
    </div>
  );
}

export default Success;
