import React, { useState } from "react";

import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import Home from "./Pages/Home";
import Success from "./Pages/Success";
import Cancel from "./Pages/Cancel";
import Failed from "./Pages/Failed";

function App() {
  const [txnid, setTxnid] = useState("")
  return (
    <>
      <Router>
        <Switch>

        <Route path="/" exact>
          {" "}
          <Home setTxnid={setTxnid} txnid={txnid} />
        </Route>
        <Route path="/payusample/success/:txn">
          {" "}
          <Success txnid={txnid} />
        </Route>
        <Route path="/payusample/canceled" component={Cancel} />
        <Route path="/payusample/failed" component={Failed} />
        </Switch>
      </Router>
    </>
  );
}

export default App;
