import React, { useState, useEffect } from "react";
import "../App.css";

import axios from "axios";
import { backend_base_url, client_base_url, TEST_URL, PRODUCTION_URL } from "../config/urls";
import { marchant_key, marchant_salt_v1 } from "../config/confidential";
import { useParams } from "react-router-dom";

function Home(props) {
  const [hash, setHash] = useState("");
  const surl = client_base_url + "success";
  const furl = client_base_url + "failed";
  const curl = client_base_url + "canceled";
  const product = {
    info: "tshirt500",
    amount: "0.01",
  };

  const params = useParams();

  const user = {
    firstname: "harish",
    phoen: "9079440677",
    email: "harishjangid0838@gmail.com",
  };
  // const hash=sha512(key|txnid|amount|productinfo|firstname|email|||||||||||SALT)
  useEffect(() => {
    axios({
      method: "post",
      // url: "https://warm-island-48799.herokuapp.com/" + "txn/",
      url: backend_base_url+"/txn/" ,
      data: {
        firstname: user.firstname,
        key: marchant_key,
        salt: marchant_salt_v1,
        amount: product.amount,
        info: product.info,
        email: user.email,
      },
    })
      .then((res) => {
        console.log(res);
        setHash(res.data.hash);
        props.setTxnid(res.data.txnID);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  return (
    <div className="App">
      {/* <button onClick={transactionHandler}> Click to Payment</button> */}
      <form action={TEST_URL} method="post">
        <input type="hidden" name="firstname" value={user.firstname} />
        <input type="hidden" name="lastname" value="" />
        <input type="hidden" name="surl" value={surl+`/${props.txnid}`} />
        <input type="hidden" name="phone" value={user.phoen} />
        <input type="hidden" name="key" value={marchant_key} />
        <input type="hidden" name="hash" value={hash} />
        <input type="hidden" name="curl" value={curl} />
        <input type="hidden" name="furl" value={furl} />
        <input type="hidden" name="txnid" value={props.txnid} />
        <input type="hidden" name="productinfo" value={product.info} />
        <input type="hidden" name="amount" value={product.amount} />
        <input type="hidden" name="email" value={user.email} />
        <input type="submit" value="Pay Now" />
      </form>


    </div>
  );
}

export default Home;

